<template>
  <div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Name</th>
          <th>Aliases</th>
          <th>Department</th>
          <th>Price</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr :exam="exam" v-for="(exam, index) in exams" :key="index">
          <td>{{ exam.name }}</td>
          <td>{{ exam.aliases.map((alias) => alias.value).join(", ") }}</td>
          <td class="is-capitalized">{{ exam.department.name }}</td>
          <td>{{ exam.price.toFixed(2) }}</td>
          <td>
            <a class="button is-link" @click="discounts(exam)">Discounts</a>
          </td>
        </tr>
      </tbody>
    </table>
    <examdiscounts-component></examdiscounts-component>
  </div>
</template>
<script>
import ExamDiscountsComponent from "@/components/ExamDiscountsComponent";

export default {
  name: "ExamsDiscountComponent",
  created() {
    this.$store.dispatch(
      "fetchDiscountExams",
      this.$store.state.authentication.inMemoryToken
    );
  },
  methods: {
      discounts(exam) {
          this.$store.dispatch("updateCashierCurrentExam", exam);
          this.$store.dispatch("toggleCashierExamModal", true);
      }
  },
  computed: {
    exams() {
      return this.$store.state.cashier.exams;
    }
  },
  components: {
      "examdiscounts-component": ExamDiscountsComponent
  }
};
</script>